import React, { useState } from 'react';
import { FormattedMessage, injectIntl } from 'gatsby-plugin-intl';

import styles from './Content.module.scss';

import Mailchimp from './Mailchimp';

import mapEN from '../assets/images/map-en.svg';
import mapES from '../assets/images/map-es.svg';
import mapPT from '../assets/images/map-pt.svg';

const Content = injectIntl(({ intl }) => {
  const maps = {
    en: mapEN,
    es: mapES,
    pt: mapPT,
  };

  return (
    <section className={styles.content}>
      {/* INTRO */}
      <div className={`container container--grid ${styles.intro}`}>
        <div className={styles.image}></div>
        <div className={styles.text}>
          <h1>
            <FormattedMessage id="title" />
          </h1>
          <p className={styles.lead}>
            <FormattedMessage id="intro_lead" />
          </p>
          <div
            dangerouslySetInnerHTML={{
              __html: intl.formatHTMLMessage({ id: 'intro_text' }),
            }}
          />
        </div>
      </div>

      {/* MAP & CTA */}
      <div className={`container container--grid ${styles.map}`}>
        <div className={styles.image}>
          <img src={maps[intl.locale]} />
        </div>
        <div className={styles.text}>
          <h2
            dangerouslySetInnerHTML={{
              __html: intl.formatHTMLMessage({ id: 'map_title' }),
            }}
          />
          <p className={styles.lead}>
            <FormattedMessage id="map_lead" />
          </p>

          <Mailchimp className={styles.mailchimp} />

          <div
            dangerouslySetInnerHTML={{
              __html: intl.formatHTMLMessage({ id: 'map_text' }),
            }}
          />
        </div>
      </div>
    </section>
  );
});

export default Content;
