import React, { useState } from 'react';
import { injectIntl } from 'gatsby-plugin-intl';

import styles from './Frame.module.scss';

import logoEN from '../assets/logos/logo-en.svg';
import logoES from '../assets/logos/logo-es.svg';
import logoPT from '../assets/logos/logo-pt.svg';

const Frame = injectIntl(({ intl }) => {
  const logos = {
    en: logoEN,
    es: logoES,
    pt: logoPT,
  };

  return (
    <section className={styles.frame}>
      <div className={`container ${styles.container}`}>
        <img className={styles.logo} src={logos[intl.locale]} />
      </div>
    </section>
  );
});

export default Frame;
