import React from 'react';
import { injectIntl } from 'gatsby-plugin-intl';
import addToMailchimp from 'gatsby-plugin-mailchimp';

import styles from './Mailchimp.module.scss';

class Mailchimp extends React.Component {
  state = {
    mailchimp: {
      idioma: null,
      email: null,
    },
    status: '',
  };

  componentDidMount() {
    // console.log(this.props.intl.locale);
    this.setState({ mailchimp: { idioma: this.props.intl.locale } });
  }

  _handleChange = (e) => {
    // console.log({
    //   [`${e.target.name}`]: e.target.value,
    // });
    // console.log(this.state);
    this.setState({
      mailchimp: {
        [`${e.target.name}`]: e.target.value,
        idioma: this.props.intl.locale,
      },
    });
  };

  _handleSubmit = (e) => {
    e.preventDefault();

    // console.log('submit', this.state);

    addToMailchimp(this.state.mailchimp.email, this.state.mailchimp)
      .then(({ msg, result }) => {
        // console.log('msg', `${result}: ${msg}`);

        if (result !== 'success') {
          throw msg;
        }
        this.setState({ status: msg });
        // alert(msg);
      })
      .catch((err) => {
        // console.log('err', err);
        this.setState({ status: err });
        // alert(err);
      });
  };

  render() {
    const { intl, className } = this.props;

    return (
      <div className={className}>
        {/* mailchimp */}
        <div className={styles.formContainer}>
          <form className={styles.reminderForm} onSubmit={this._handleSubmit}>
            <input
              type="email"
              onChange={this._handleChange}
              placeholder="e-mail"
              name="email"
            />
            <button className={styles.btnSubmit} type="submit">
              
            </button>
            <p
              className={styles.status}
              dangerouslySetInnerHTML={{
                __html: this.state.status,
              }}
            />
          </form>
        </div>
      </div>
    );
  }
}

export default injectIntl(Mailchimp);
